
import logo from '../../images/logo.png'
import Logbtn from '../logBtn/logbtn'
import Reqbtn from '../reqbtn/reqbtn'
import { Link } from 'react-scroll';


function Footer(props){
  return(
    <div id='Footer' className="Footer">
      <div className="itemFOT btnfotinfo">
          <Link to='sec1' smooth={true} duration={1000}>
            <button></button>
          </Link>
      </div>
      <div className="itemFOT containfot">
        <div className="itemcontionR">
          <img loading='lazy' className='itemR itemrlogo' src={logo} alt="logo" />
          <div dir='rtl' className="itemR">   <p></p> !      !</div>
          <div className="itemR itemRinfofoot">:    </div>
        </div>
        <div className="itemcontionL">
          <Logbtn link='#' intro='  ' />
          <Reqbtn link='#' intro='  '/>
          </div>
      </div>
      <hr />
      <div className="itemFOT foot">
        <div className="fotlist">
          <Link smooth={true} duration={1000} to='sec1' className="fotlistitem"> </Link>
          <Link smooth={true} duration={1000} to='sec3' className="fotlistitem">  </Link>
          <Link smooth={true} duration={1000} to='sec5' className="fotlistitem"> </Link>
          <a href={props.loglink} className="fotlistitem">  </a>
        </div>
        <div dir='rtl' className="fotcomp">      <a className='linkfot' href={props.link}>   </a> .</div>
      </div>
    </div>
  )
}

export default Footer